body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
    font-size: 44px;
}

.square {
    background: #fff;
    border: 2px solid black;
    float: left;
    font-size: 44px;

    line-height: 34px;
    height: 100px;
    margin-right: -2px;
    margin-top: -2px;
    padding: 0;
    text-align: center;
    width: 100px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
    horiz-align: center;
}

.game-info {
    margin-left: 20px;
}
.new-game {
    width: 302px;
    margin-left: -2px;
    margin-top: 10px;
    font-size: 24px;
    border-radius: 15px;
    border-color: rgba(100,200,300,0.5);
    padding: 5px;
}

